  }
}

.marquee-scroll-left {
  width: 100%;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.marquee-scroll-left > * {
  display: inline-block;
  padding-left: 100%;
  text-indent: 0;
  animation: marquee-scroll-left 15s linear infinite;
}

@keyframes marquee-scroll-left {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-100%, 0);
  }
}

.marquee-scroll-right {
  width: 100%;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.marquee-scroll-right > * {
  display: inline-block;
  padding-right: 100%;
  text-indent: 0;
  animation: marquee-scroll-right 15s linear infinite;
}

@keyframes marquee-scroll-right {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(100%, 0);
  }
}

.marquee-bounce-left {
  width: 100%;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.marquee-bounce-left > * {
  display: inline-block;
  padding-left: 100%;
  text-indent: 0;
  animation: marquee-bounce-left 15s linear infinite alternate;
}

@keyframes marquee-bounce-left {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(-90%, 0);
  }
}

.marquee-bounce-right {
  width: 100%;
  margin: 0 auto;
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.marquee-bounce-right > * {
  display: inline-block;
  padding-right: 100%;
  text-indent: 0;
  animation: marquee-bounce-right 15s linear infinite alternate;
}

@keyframes marquee-bounce-right {
  0% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(90%, 0);
  }
}

.rabbit-run {
  white-space: nowrap;
  overflow: hidden;
  box-sizing: border-box;
}

.rabbit-run > * {
  display: inline-block;
  text-indent: 0;
  padding-right: 80%;
  animation: rabbit-run 3540ms linear infinite;
}

@keyframes rabbit-run {
  0% {
    transform: translate(-10%, 0);
  }

  5% {
    transform: translate(0, 0);
  }

  58% {
    transform: translate(0, 0);
  }

  100% {
    transform: translate(120%, 0);
  }
}

